
.com_gray1 {
  color: #666;
}
.com_gray2 {
  color: #999;
}
.com_blue {
  color: #0096ff;
}
.index_main {
  width: 10rem;
  overflow: hidden;
}
.index_main .index_header {
  background: #0096ff;
  height: 1.2rem;
  padding: .26rem;
  box-sizing: border-box;
  overflow: hidden;
  /* 搜索 */
  /* 热搜 */
}
.index_main .index_header .index_location {
  height: .9rem;
  color: #fff;
  width: 10rem;
  font-size: .4rem;
}
.index_main .index_header .index_location .location_left {
  width: 9rem;
}
.index_main .index_header .index_location .location_left svg {
  width: .5rem;
  height: .5rem;
}
.index_main .index_header .index_location .index_login {
  position: absolute;
  height: 1rem;
  font-size: .35rem;
  right: 0;
  top: 0;
  line-height: 1rem;
  padding: 0 .2rem;
}
.index_main .index_header .index_location .index_login a {
  color: #fff;
}
.index_main .index_header .search_box {
  width: 9rem;
  margin: 0 auto;
  height: .9rem;
}
.index_main .index_header .search_box input[type="text"] {
  width: 100%;
  height: .9rem;
  outline: none;
  font-size: .4rem;
  text-align: center;
  border: none;
  display: block;
  border-radius: .45rem;
}
.index_main .index_header .hot_word {
  height: 1.2rem;
  line-height: .9rem;
  font-size: .3rem;
  width: 9.48rem;
  overflow-x: auto;
  white-space: nowrap;
}
.index_main .index_header .hot_word span {
  padding: 0 .1rem;
}
.index_main .index_header .hot_word a {
  color: #fff;
}
.index_main .index_banner {
  height: 4.6rem;
  background: #fff;
  margin-bottom: 0.2rem;
}
.index_main .index_banner .common_slider {
  width: 2.5rem;
  height: 2rem;
  float: left;
}
.index_main .index_banner .common_slider img {
  margin: 0 auto;
  display: block;
  width: 1.2rem;
  height: 1.2rem;
  margin-top: .2rem;
}
.index_main .index_banner .common_slider p {
  text-align: center;
  font-size: .3rem;
}
.index_main .index_title {
  line-height: .8rem;
  font-weight: 600;
  background: #fff;
  border-top: 1px solid #eee;
  font-size: .4rem;
  padding-left: .4rem;
}
.index_main .space {
  width: 10rem;
  height: auto;
}
.index_main .space .hide-group {
  visibility: hidden;
  position: absolute;
  z-index: 999;
  top: 0;
}
.index_main .space .menu-group {
  /*padding: 0 0.1rem;*/
  margin-top: 0.3rem;
  position: relative;
}
.index_main .space .menu-group:last-child {
  margin-top: 1.2rem;
}
.index_main .space .menu-group img {
  display: inline-block;
}
.index_main .space .menu-group .title {
  display: inline-block;
  font-size: 0.5rem;
  margin-bottom: 0.35rem;
  margin-left: -0.15rem;
  vertical-align: middle;
}
.index_main .space .menu-group .btn-group {
  position: absolute;
  right: 0.4rem;
  top: -0.2rem;
}
.index_main .space .menu-group .menu-list {
  /*width: 100%;*/
  padding: 0 0.1rem;
}
.index_main .space .menu-group .menu-list p {
  font-weight: normal;
  font-size: 0.4rem;
}
.index_main .space .menu-group .menu-list .menuImage {
  width: 1.8rem;
  height: 1.8rem;
}
.index_main .space .menu-group .btn {
  display: inline-block;
  font-size: 0.4rem;
  border: 1px solid #2193fc;
  color: #2193fc;
  border-radius: 0.1rem;
  padding: 0.1rem 0.2rem;
  margin-left: 0.5rem;
}
.index_main .space .menu-group .btn.green {
  border: 1px solid #69c447;
  color: #69c447;
}
.index_main .space .menu-group .btn.gray {
  border: 1px solid #cccccc;
  color: #cccccc;
}
.index_main .space .menu-group .btn.red {
  border: 1px solid #F56C6C;
  color: #F56C6C;
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.footer .tab {
  /*display: inline-block;*/
  width: 49%;
  display: -ms-flexbox;
  display: flex;
  float: left;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-bottom: 0.2rem;
}
.footer .tab .tab-title {
  margin-left: 0.2rem;
  font-size: 0.4rem;
}
.footer .tab .tab-title.select {
  color: #efb336;
}
/* slider插件css + 改写*/
.mint-swipe,
.mint-swipe-items-wrap {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.mint-swipe-items-wrap > div {
  position: absolute;
  transform: translateX(-100%);
  width: 100%;
  height: 100%;
  display: none;
}
.mint-swipe-items-wrap > div.is-active {
  display: block;
  transform: none;
}
.mint-swipe-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}
.mint-swipe-indicator {
  width: .1rem;
  height: .1rem;
  display: inline-block;
  border-radius: 50%;
  background: #ccc;
  margin: 0 3px;
}
.mint-swipe-indicator.is-active {
  background: #0096ff;
}



